import { createHashHistory } from 'history';
import React from 'react';
import { Route, Router, Switch } from 'react-router-dom';
import { Game } from 'sections/Main/Game/Game';
import { Content } from 'sections/Main/Sandbox/Content';
import { FormPage } from 'sections/Main/Sandbox/FormPage';

export const Main: React.FC = () => {
  const history = createHashHistory();

  return (
    <Router history={history}>
      <Switch>
        <Route path="/form" exact={true} component={() => <FormPage />} />
        <Route path="/content" exact={true} component={() => <Content />} />
        <Route path="/submitted" exact={true} component={() => <SubmittedPage />} />
        <Route path="/page-3" exact={true} component={() => <DummyPage pageNumber={3} />} />

        <Route path="/" component={Game} />
      </Switch>
    </Router>
  );
};

const DummyPage: React.FC<{ pageNumber: number }> = ({ pageNumber }) => {
  const sendEvent = () => {
    window.gtag('event', 'bar', {
      event_category: 'test_category',
      event_label: 'test_label',
    });
  };

  return (
    <div className="w-100 text-center">
      <h1>Page-{pageNumber}</h1>
      <a href={`#/`}>HOME</a>
      <br />
      <button onClick={sendEvent}>foo</button>
    </div>
  );
};

const SubmittedPage: React.FC = () => {
  return (
    <div className="mt-4 text-center">
      <h3>Submitted</h3>

      <br />

      <a href="#/">Home</a>
    </div>
  );
};
