import React from 'react';
import ReactDOM from 'react-dom';
import { App } from 'App';
import './index.scss';
import GA4React from 'ga-4-react';

const gaTrackingId = 'G-1K6KGFYSH7';
const ga4react = new GA4React(gaTrackingId);

(async _ => {
  await ga4react.initialize();

  ReactDOM.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>,
    document.getElementById('root')
  );
})();
